.item {
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  padding: 10px;
}

.item:last-of-type {
  border-bottom: none;
}

.users {
  margin: 20px 0;
}

.buttonsContainer {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
}

.container {
  max-width: 800px;
  margin: 0 auto;
}

.thead {
  background-color: #ddd;
  font-weight: bold;
}

.nameCol {
  font-weight: bold;
  text-align: left;
  padding-right: 100px;
}

.ageCol {
  text-align: start;
  padding-right: 100px;
}