.buttonsContainer {
    display: flex;
    margin-top: 20px;
    
  }
  .buttonsContainer button {
    width: 150px;
    margin-right: 20px;
    font-size: large;
  }
  .clock {
    display: flex;
    flex-direction: column;
    font-size: 40px;
    margin-top: 40px;
    
  }
  
  .watch {
    display: flex;
    margin-top: 20px;
  }
  
  .watch strong {
    margin-right: 10px;
    font-size: 20px;
  }
  
  .more {
    display: flex;
    margin-top: 20px;
  }
  