.select {
    position: relative;
    width: 200px;
    height: 40px;
    background-color: #fff;
    border: 2px solid #ccc;
    border-radius: 4px;
    padding: 8px;
    font-size: 16px;
    cursor: pointer;
    margin-right: 20px;
  }
  
  .select:focus {
    outline: none;
    border-color: #333;
    box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.1);
  }
  
  .option:hover {
    background-color: #f5f5f5;
    color: #333;
    cursor: pointer;
  }