.radio {
  display: inline-block;
  position: relative;
  margin-right: 10px;
  background-color: #070606;
  border-color: #0e0404;
}

.radio::before {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid #ccc;
  background-color: #070606;
  border-color: #0e0404;
}

.radio:checked::before {
  background-color: #333;
  border-color: #333;
}

.label {
  display: block;
  
  border-color: #0e0404;
}

.options {
  margin-top: 10px;
}